.sidebarRow{
    display: flex;
    padding: 5px 20px;
    align-items: center;
    margin-bottom: 10px;
    position: sticky;
}

.sidebarRow p{
    margin-left: 10px;
    font-weight: bold;
    font-size: 14px;
}

.sidebarRow:hover{
    background-color: rgb(211 211 211/31%);
    cursor: pointer;
}