.header{
    display: flex;
    padding: 5px 20px;
    background-color: #ffffff;
    position: sticky;
    top: 0;
    z-index: 99;
    justify-content: space-between;
    box-shadow: 0px 0px 3px #ccc;
}

.header__left{
    display: flex;
    align-items: center;
}

.header__left img{
    height: 40px;
}

.header__search{
    display: flex;
    align-items: center;
    background-color: whitesmoke;
    border-radius: 20px;
    padding: 0px 10px;
    height: 40px;
    margin-left: 10px;
}

.header__search input{
   border: 0;
   outline: 0;
   background-color: transparent;
   min-width: 200px;
}

.header__middle{
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__option{
    padding: 5px 40px;
    cursor: pointer;
}

.header__option:hover{
    border-radius: 20px;
    background-color: whitesmoke;
}

.header__option svg{
    color: gray;
}

.header__option svg:hover{
    color:#2381fa;
}

.header__option--active svg{
    color: #2381fa;
}

.header__option--active{
    position: relative;
}
.header__option--active::after{
   content: '';
   position: absolute;
   height: 3px;
   left: 0;
   right: 0;
   bottom: -5px;
   background-color: #2381fa;
}

.header__right{
    display: flex;
    align-items: center;
}

.header__info{
    display: flex;
    align-items: center;
}

.header__info h5{
    margin-right: 20px;
}

.header__info .MuiAvatar-root{
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.header__right .MuiButtonBase-root{
   width: 40px;
   height: 40px;
   margin: 0px 5px ;
   background-color: whitesmoke;
   color: #000;
}
